.contact-main-container {
    background: -webkit-linear-gradient(left, rgba(148, 222, 247, 0.4) 40%, rgba(138, 255, 183, 0.3) 80%);
    font-family: 'Poppins', sans-serif;
    padding-top: 50px;
    padding-bottom: 100px;
}

.contact-head {
    color: #00a37a;
    font-size: 6em;
    font-weight: 900;
    text-align: center;
}

.contact-wrapper {
    display: flex;
    flex-wrap: wrap; /* Enable wrapping */
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px;
    height: 100%;
}

.contact-form {
    width: 45%; /* Form takes 45% of the width on larger screens */
    max-width: 100%; /* Ensure form doesn't exceed the screen width */
    background-color: white;
    border: 2px solid #32cd9c;
    border-radius: 10px;
    box-shadow: 10px 4px 25px rgba(50, 205, 156, 1);
    padding: 20px;
    text-align: left;
    position: relative;
    box-sizing: border-box; /* Prevents overflow due to padding */
}

.contact-form h2 {
    margin-bottom: 20px;
    text-align: center;
    text-transform: uppercase;
}

.contact-form label {
    display: block;
    font-size: 14px;
    color: #333;
    margin-bottom: 5px;
}

.contact-form input,
.contact-form textarea {
    width: calc(100% - 20px);
    padding: 10px;
    border: 2px solid #32cd9c;
    border-radius: 5px;
    margin-bottom: 20px;
    font-size: 16px;
    outline: none;
    box-shadow: inset 0 1px 3px rgba(50, 205, 156, 0.3);
}

.contact-form textarea {
    height: 100px;
    resize: vertical;
}

.contact-btn {
    border: none;
    cursor: pointer;
    text-transform: uppercase;
    color: rgb(0, 0, 0);
    background-color: #32cd9c;
    margin: 15px 0;
    font-size: 16px;
    width: 100%;
    padding: 14px;
    border-radius: 5px;
}

.contact-btn:hover {
    background-color: #1f7d5f;
    color: white;
}

.contact-social {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 20px;
}

.contact-social a {
    color: #32cd9c;
    font-size: 20px;
    text-decoration: none;
}

.contact-social a:hover {
    color: #1f7d5f;
}

.contact-map {
    width: 45%; /* Map takes 45% of the width on larger screens */
    display: flex;
    justify-content: center;
    align-items: center;
}

.map-container {
    border-radius: 10px;
    border: 2.5px solid rgb(50, 177, 205);
    box-shadow: -10px 4px 25px rgb(50, 177, 205);
    overflow: hidden;
}

.contact-map iframe {
    border-radius: 10px;
    border: 3px solid lightblue;
    height: 750px; /* Default height */
}

/* Responsive design for smaller screens */

@media screen and (max-width: 1200px) {
    .contact-wrapper {
        flex-direction: column; /* Stack form and map */
        align-items: center;
    }

    .contact-form {
        width: 90%; /* Increase form width on smaller screens */
        margin-bottom: 30px;
        /* overflow: hidden; */
    }

    .contact-map {
        width: 100%; /* Increase map width on smaller screens */
        /* overflow: hidden; */
    }

    .contact-map iframe{
        width: 85vw;
        height: 500px;
    }

    .contact-head {
        font-size: 4.5em; /* Reduce font size */
    }
}

@media screen and (max-width: 800px) {
    .contact-form {
        width: 95%;
    }

    .contact-map iframe {
        height: 450px; /* Reduce map height */
    }

    .contact-head {
        font-size: 3.5em; /* Further reduce font size */
    }
}

@media screen and (max-width: 600px) {
    .contact-form {
        padding: 15px; /* Reduce padding for smaller screens */
    }

    .contact-map iframe {
        height: 400px; /* Further reduce map height */
    }

    .contact-btn {
        font-size: 14px; /* Smaller button font size */
        padding: 12px;
    }

    .contact-head {
        font-size: 3em; /* Adjust heading size */
    }
}

@media screen and (max-width: 400px) {
    .contact-form {
        padding: 10px;
    }

    .contact-map iframe {
        height: 350px; /* Minimum height */
    }

    .contact-head {
        font-size: 2.5em; /* Further reduce heading size */
    }
}
