.events-page {
    position: relative;
}

.background-slider {
    overflow: hidden;
    height: 540px;
    /* Adjust height as needed */
    position: relative;
}

.slides {
    display: flex;
    transition: transform 0.5s ease-in-out;
}

.slide {
    width: 100%;
    height: auto;
}

.event-header {
    font-family: 'ICA Rubrik';
    font-size: 5em;
    line-height: 1em;
    color: #25283B;
    position: relative;
    text-align: center;
    text-transform: capitalize;
    margin-bottom: 20px;
    margin-top: 50px;
}

.image-gallery,
#download-image-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
}

.image-gallery img,
#download-image-gallery img {
    width: calc(20% - 10px);
    /* 5 images per row with some margin */
    margin: 5px;
}

/* Default for large screens (4K and up) */
@media (min-width: 2560px) {
    .background-slider {
        height: 540px;
    }

    .event-header {
        font-size: 6em;
    }

    .image-gallery img,
    #download-image-gallery img {
        width: calc(16.66% - 10px);
        /* 6 images per row */
    }
}

/* For desktops and large laptops */
@media (min-width: 1440px) and (max-width: 2559px) {
    .background-slider {
        height: 100%;
    }

    .event-header {
        font-size: 5em;
    }

    .image-gallery img,
    #download-image-gallery img {
        width: calc(20% - 10px);
        /* 5 images per row */
    }
}

/* For medium-sized screens (laptops) */
@media (min-width: 1024px) and (max-width: 1439px) {
    .background-slider {
        height: 100%;
    }

    .event-header {
        font-size: 4em;
    }

    .image-gallery img,
    #download-image-gallery img {
        width: calc(25% - 10px);
        /* 4 images per row */
    }
}

/* For tablets */
@media (min-width: 768px) and (max-width: 1023px) {
    .background-slider {
        height: 100%;
    }

    .event-header {
        font-size: 3em;
    }

    .image-gallery img,
    #download-image-gallery img {
        width: calc(33.33% - 10px);
        /* 3 images per row */
    }
}

/* For large mobile screens */
@media (min-width: 576px) and (max-width: 767px) {
    .background-slider {
        height: 100%;
    }

    .event-header {
        font-size: 2.5em;
    }

    .image-gallery img,
    #download-image-gallery img {
        width: calc(50% - 10px);
        /* 2 images per row */
    }
}

/* For small mobile screens */
@media (max-width: 575px) {
    .background-slider {
        height: 100%;
    }

    .event-header {
        font-size: 2em;
    }

    .image-gallery img,
    #download-image-gallery img {
        width: 100%;
        /* 1 image per row */
    }
}

/* Ultra small screens (200px - 399px) */
@media (max-width: 399px) {
    .background-slider {
        height: 100%;
    }

    .event-header {
        font-size: 1.5em;
    }

    .image-gallery img,
    #download-image-gallery img {
        width: 100%;
        /* 1 image per row */
    }
}

/* Loading overlay inside the events page boundaries */
.loading-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    padding-top: 32vh;
    z-index: 10;
}

.loading-spinner {
    font-size: 2em;
    color: #ffffff;
}

/* Preview overlay */
.event-preview-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10001;
    overflow: hidden; 
}

.event-preview-image {
    max-width: 90%;
    max-height: 90%;
    border-radius: 8px;
}

/* Close button styling */
.event-close-button {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 2em;
    color: white;
    background: none;
    border: none;
    cursor: pointer;
}

/* Responsive gallery layout */
.image-gallery img {
    cursor: pointer;
}

@media (max-width: 575px) {
    .image-gallery img {
        width: calc(50% - 10px);
    }
}