/* Volunteer.css */

.volunteer-page-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.volunteer-content {
    z-index: 2;
    /* Keep text above the gradient and background */
    padding: 20px;
    animation: fadeIn 2s ease-in-out;
    text-align: center;
}

.volunteer-title {
    font-size: 5rem;
    margin-bottom: 40px;
    color: #fff;
    font-family: 'Poppins', sans-serif;
    /* White text for contrast */
}

.volunteer-description {
    font-size: 2rem;
    color: #fff;
    margin-bottom: 30px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.volunteer-button {
    display: inline-block;
    padding: 15px 30px;
    font-size: 1.5rem;
    color: #fff;
    background-color: #007bff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s, transform 0.3s;
}

.volunteer-button:hover {
    background-color: #0056b3;
    transform: scale(1.05);
}

.volunteer-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('https://cdn.pixabay.com/photo/2017/02/10/12/12/volunteer-2055043_640.png');
    /* Replace with your background image */
    background-size: cover;
    background-position: center;
    animation: backgroundAnimation 10s ease infinite;
    opacity: 0.4;
    /* Faded effect */
    z-index: 1;
    /* Behind the text */
}

.volunteer-gradient-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(-45deg, rgba(126, 71, 255, 0.5), rgba(236, 127, 255, 0.5));
    animation: gradientAnimation 1s ease infinite;
    z-index: 1;
    /* Above the background image but below the text */
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes backgroundAnimation {
    0% {
        transform: scale(1.05);
    }

    50% {
        transform: scale(1.0);
    }

    100% {
        transform: scale(1.05);
    }
}

@keyframes gradientAnimation {
    0%{
        background-position: 0% 25%;
    }
    25%{
        background-position: 25% 50%;
    }
    50%{
        background-position: 50% 75%;
    }
    75%{
        background-position: 75% 85%;
    }
    100%{
        background-position: 85% 100%;
    }
}