.footer-section {
    background-color: #3f9fff;
    color: #fff;
    padding: 40px 20px;
    position: relative;
    overflow: hidden;
}

/* Container for the spheres */
.footer-spheres {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0; /* Ensure the spheres are behind the content */
    overflow: hidden;
}

.footer-sphere {
    position: absolute;
    /* border-radius: 20%; */
    background: rgba(4, 0, 255, 0.2);
    animation: float 8s infinite ease-in-out;
}

.footer-sphere:nth-child(1) {
    width: 80px;
    height: 80px;
    top: 50%;
    left: 5%;
    border-radius: 30%;
    animation-duration: 6s;
}

.footer-sphere:nth-child(2) {
    width: 100px;
    height: 100px;
    top: 30%;
    right: 15%;
    border-radius: 30%;
    animation-duration: 15s;
}

.footer-sphere:nth-child(3) {
    width: 150px;
    height: 150px;
    bottom: -8%;
    left: 50%;
    border-radius: 50%;
    animation-duration: 10s;
}

.footer-sphere:nth-child(4) {
    width: 120px;
    height: 120px;
    top: 10%;
    left: 30%;
    border-radius: 50%;
    animation-duration: 10s;
}

@keyframes float {
    0% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-50px);
    }
    100% {
        transform: translateY(0px);
    }
}

.footer-container {
    position: relative;
    z-index: 1; /* Ensure content is above the spheres */
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

/* First Line: Links */
.footer-links {
    display: flex;
    gap: 10px;
    font-size: 18px;
    justify-content: center;
    flex-wrap: wrap;
    white-space: nowrap;
}

.footer-links a {
    color: #ebfff9;
    text-decoration: none;
    text-transform: uppercase;
}

.footer-links a:hover {
    text-decoration: underline;
    font-weight: bolder;
}

/* Second Line: Paragraph on the left and Social Icons on the right */
.footer-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.footer-paragraph {
    display: flex;
    font-size: 18px;
    justify-content: flex-start;
    flex: 1;
    text-align: justify;
    margin-right: 50px;
    margin-left: 40px;
    line-height: 1.5;
}

.footer-social-icons {
    margin-top: -10px;
    display: flex;
    flex: 0;
    gap: 20px;
    margin-right: 40px;
    justify-content: flex-end;
}

.footer-social-icons a {
    font-size: 24px;
    color: #ebfff9;
    text-decoration: none;
}

.footer-social-icons a:hover {
    color: #ccc;
    transition: 100ms;
}

/* Third Line: Author centered */
.footer-author {
    font-size: 20px;
    color: #eee;
    text-align: center;
    margin-top: 10px;
}

.un-author {
    display: inline-block;
    padding-bottom: 2px;
    background-image: linear-gradient(#ff86f9 0 0);
    background-position: 0% 100%;
    background-size: 0% 3px;
    background-repeat: no-repeat;
    transition:
        background-size 0.3s,
        background-position 0s 0.3s;
}

.un-author:hover {
    background-position: 100% 100%;
    background-size: 100% 3px;
    background-image: linear-gradient(rgb(255, 108, 184) rgb(234, 101, 101) rgb(191, 88, 255));
}

.author-url{
    text-decoration: none;
    color: #e5d7ff;
}

/* Larger text and better font styles */
.footer-paragraph, .footer-links a, .footer-author p {
    font-family: 'Poppins', sans-serif;
}

/* Media Queries for Responsiveness */

/* For screens under 1000px */
@media (max-width: 1000px) {
    .footer-paragraph {
        font-size: 16px;
        max-width: 100%;
    }

    .footer-social-icons {
        justify-content: center;
        margin-top: -20px;
    }

    .footer-links {
        font-size: 16px;
    }
}

/* For screens under 768px */
@media (max-width: 768px) {
    .footer-content {
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }

    .footer-paragraph {
        text-align: justify;
        margin-left: 0;
        margin-right: 0;
    }

    .footer-social-icons {
        justify-content: center;
        margin-right: 0;
        margin-top: 0px;
    }

    .footer-links {
        font-size: 14px;
    }
}

/* For screens under 400px */
@media (max-width: 400px) {
    .footer-links {
        font-size: 12px;
        align-items: center;
    }

    .footer-social-icons {
        gap: 15px;
        justify-content: center;
    }

    .footer-paragraph {
        font-size: 12px;
    }

    .footer-author {
        font-size: 14px;
    }
}
