@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:wght@400;700&display=swap');

/* Main container for the error page */
.error-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #111;
  color: white;
  padding: 2%;
  font-family: 'Comic Neue', cursive;
  font-style: italic;
  font-weight: 800;
  text-align: center;
  height: 100%;
  min-height: 100vh;
}

/* Fade-in background effect */
@keyframes fadeInBackground {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* Video container */
.error-video-container {
  width: 80%;
  max-width: 1000px;
  margin-bottom: 20px;
  opacity: 0;
  transition: opacity 2s ease-in;
}

/* Show video with fade-in effect */
.error-video-container.show {
  opacity: 1;
}

/* Video styling */
.error-video {
  width: 100%;
  height: auto;
  border-radius: 8px;
  object-fit: cover;
}

/* Message text container */
.error-message {
  opacity: 0;
  transition: opacity 2s ease-in;
  max-width: 80%;
}

/* Show message text with fade-in effect */
.error-message.show {
  opacity: 1;
}

/* Heading, paragraph, and button styles */
.error-header {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.error-para {
  font-size: 1.25rem;
  margin-bottom: 20px;
}

.error-button {
  padding: 12px 24px;
  font-size: 1.2rem;
  cursor: pointer;
  background: linear-gradient(to right, #3dd1d4, #3086ff, #3c4cf6);
  color: white;
  border: none;
  border-radius: 5px;
  font-family: 'Comic Nueu', cursive;
  font-style: italic;
  font-weight: bold;
}

.error-button:hover {
  background: linear-gradient(to right, #d43d3d, #ba4303, #ab7708);
}

/* 4K screens */
@media (max-width: 3840px) {
  .error-header { font-size: 3rem; }
  .error-para { font-size: 1.5rem; }
  .error-button { font-size: 1.3rem; }
}

/* Large desktops */
@media (max-width: 1920px) {
  .error-header { font-size: 2.5rem; }
  .error-para { font-size: 1.3rem; }
  .error-button { font-size: 1.2rem; }
}

/* Laptops and desktops */
@media (max-width: 1440px) {
  .error-header { font-size: 2.2rem; }
  .error-para { font-size: 1.2rem; }
  .error-button { font-size: 1.1rem; }
}

/* Tablets */
@media (max-width: 1024px) {
  .error-video-container { width: 90%; }
  .error-header { font-size: 2rem; }
  .error-para { font-size: 1.1rem; }
  .error-button { font-size: 1rem; padding: 10px 20px; }
}

/* Smaller tablets and large phones */
@media (max-width: 768px) {
  .error-header { font-size: 1.8rem; }
  .error-para { font-size: 1rem; }
  .error-button { font-size: 1rem; padding: 8px 16px; }
}

/* Portrait mobile */
@media (max-width: 576px) {
  .error-video-container { width: 100%; margin-bottom: 15px; }
  .error-header { font-size: 1.5rem; }
  .error-para { font-size: 0.9rem; }
  .error-button { font-size: 0.9rem; padding: 8px 14px; }
}

/* Small mobile screens */
@media (max-width: 320px) {
  .error-header { font-size: 1.3rem; }
  .error-para { font-size: 0.8rem; }
  .error-button { font-size: 0.8rem; padding: 6px 12px; }
}

/* Extra small screens (down to 200px) */
@media (max-width: 200px) {
  .error-header { font-size: 1rem; }
  .error-para { font-size: 0.7rem; }
  .error-button { font-size: 0.7rem; padding: 4px 10px; }
}
