.hero-banner {
  background: url('https://img.freepik.com/free-photo/wide-angle-shot-single-tree-growing-clouded-sky-sunset-surrounded-by-grass_181624-22807.jpg?t=st=1724986682~exp=1724990282~hmac=74ba92590ae14ba0d53961a2b2a6ead33d1b098ec060958dac397b01add5bdc0&w=900') center/cover no-repeat;
  color: #fff;
  position: relative;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 55vh;
}

.hero-banner h1 {
  font-size: 3vw;
  margin-bottom: 1rem;
}

.hero-banner p {
  font-size: 1.5vw;
  margin-bottom: 2rem;
}

.cta-button {
  padding: 12px 16px;
  background-color: #f9a825;
  color: #333;
  border: none;
  border-radius: 0.5em;
  cursor: pointer;
  font-size: 1.2vw;
}

.cta-button:hover {
  background-color: #ffd54f;
}

