.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: overpara 300ms ease-in forwards;
}

@keyframes overpara {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

.overlay-box {
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  position: relative;
  width: 700px;
  max-width: 80%;
  text-align: center;
  box-sizing: border-box;
}

.close-button {
  position: absolute;
  top: 0px;
  right: 10px;
  font-size: 50px;
  background: none;
  border: none;
  color: #000;
  cursor: pointer;
  animation: close 500ms ease-in backwards;
}

@keyframes close {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-90deg);
  }
}

.close-button:hover {
  color: red;
  animation: cross 500ms ease-out forwards;
}

@keyframes cross {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(90deg);
  }
}

.overlay-box p {
  font-size: 20px;
  color: #333;
  text-align: justify;
  padding-top: 20px;
  line-height: 1.5;
}

