@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

#activities-section {
  padding: 20px;
  background-color: #f9f9f9;
}

.activities-head {
  font-family: 'ICA Rubrik';
  font-size: 7em;
  line-height: 1em;
  color: #25283B;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  margin-top: 30px;
}

.slider-text-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 50px;
}

.slider-text-container.reverse {
  flex-direction: row-reverse;
}

.slider {
  flex: 1;
  max-width: 820px;
  margin: 0 20px;
  padding-left: 20px;
  padding-top: 100px;
  margin-right: 50px;
  color: #fff;
}

.slider-reverse {
  flex: 1;
  max-width: 820px;
  margin: 0 20px;
  padding-left: 20px;
  padding-top: 100px;
  margin-right: 50px;
  color: #fff;
}

.text-activities-1 {
  flex: 1;
  max-width: 600px;
  max-height: 600px;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  margin-top: 100px;
  margin-right: 80px;
  margin-left: 0px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  font-family: 'Poppins', sans-serif;
}

.text-activities-1 h2 {
  margin-bottom: 10px;
  font-size: 24px;
  text-align: center;
}

.text-activities-1 p {
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
  text-align: justify;
}

.text-activities-2 {
  flex: 1;
  max-width: 600px;
  max-height: 600px;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  margin-top: 100px;
  margin-right: 10px;
  margin-left: 40px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  font-family: 'Poppins', sans-serif;
}

.text-activities-2 h2 {
  margin-bottom: 10px;
  font-size: 24px;
  text-align: center;
}

.text-activities-2 p {
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
  text-align: justify;
}

.text-activities-3 {
  flex: 1;
  max-width: 600px;
  max-height: 600px;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  margin-top: 100px;
  margin-right: 80px;
  margin-left: 0px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  font-family: 'Poppins', sans-serif;
}

.text-activities-3 h2 {
  margin-bottom: 10px;
  font-size: 24px;
  text-align: center;
}

.text-activities-3 p {
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
  text-align: justify;
}

.swiper {
  width: 100%;
  height: 550px;
  color: #fff;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.autoplay-progress {
  position: absolute;
  right: 16px;
  bottom: 16px;
  z-index: 10;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: rgb(0, 217, 255);
}

.autoplay-progress svg {
  --progress: 0;
  position: absolute;
  left: 0;
  top: 0px;
  z-index: 10;
  width: 100%;
  height: 100%;
  stroke-width: 4px;
  stroke: rgb(0, 217, 255);
  fill: none;
  stroke-dashoffset: calc(160px * (1 - var(--progress)));
  stroke-dasharray: 145px;
  transform: rotate(-90deg);
  color: #fff;
}

.autoplay-progress-1 {
  position: absolute;
  right: 16px;
  bottom: 16px;
  z-index: 10;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: #fff;
}

.autoplay-progress-1 svg {
  --progress: 0;
  position: absolute;
  left: 0;
  top: 0px;
  z-index: 10;
  width: 100%;
  height: 100%;
  stroke-width: 4px;
  stroke: #fff;
  fill: none;
  stroke-dashoffset: calc(160px * (1 - var(--progress)));
  stroke-dasharray: 145px;
  transform: rotate(-90deg);
  color: #fff;
}

.autoplay-progress-2 {
  position: absolute;
  right: 16px;
  bottom: 16px;
  z-index: 10;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: #fff;
}

.autoplay-progress-2 svg {
  --progress: 0;
  position: absolute;
  left: 0;
  top: 0px;
  z-index: 10;
  width: 100%;
  height: 100%;
  stroke-width: 4px;
  stroke: #fff;
  fill: none;
  stroke-dashoffset: calc(160px * (1 - var(--progress)));
  stroke-dasharray: 145px;
  transform: rotate(-90deg);
  color: #fff;
}

.slider-text-container:nth-child(3) {
  flex-direction: row-reverse;
}

@media screen and (max-width: 1450px){
  .activities-head{
    font-size: 6.8rem;
  }

  .slider, .slider-reverse {
    max-width: 50%;
    padding-left: 0;
    padding-top: 20px;
    margin: 20px 0;
  }

  .text-activities-1, .text-activities-3 {
    max-width: 50%;
    padding: 30px;
    margin: 20px 0;
    font-size: 16px;
    margin-left: 20px;
  }

  .text-activities-2{
    max-width: 50%;
    padding: 30px;
    margin: 20px 0;
    font-size: 16px;
    margin-right: 20px;
  }
}


/* General layout for screens starting from 1000px down to 400px */
@media screen and (max-width: 1000px) {
  .activities-head{
    font-size: 6rem;
  }

  .slider-text-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  /* Reverse container - stacking the second slider before text */
  .slider-text-container.reverse {
    flex-direction: column-reverse;
  }

  .slider-text-container:nth-child(3) {
    flex-direction: column;
  }

  /* Slider adjustments */
  .slider, .slider-reverse {
    max-width: 90%;
    padding-left: 0;
    padding-top: 20px;
    margin: 20px 0;
  }

  /* Text box adjustments */
  .text-activities-1, .text-activities-2, .text-activities-3 {
    max-width: 90%;
    padding: 15px;
    margin: 20px 0;
    font-size: 16px;
  }

  /* Modify headings */
  .text-activities-1 h2, .text-activities-2 h2, .text-activities-3 h2 {
    font-size: 22px;
  }

  /* Swiper image and height adjustments */
  .swiper {
    height: 350px;
  }
  
  .swiper-slide img {
    height: 100%;
    object-fit: contain;
  }

  /* Specific fix for slider-reverse and text-activities-2 */
  .slider-reverse {
    padding-top: 50px;
    margin-right: 20px;
  }

  .text-activities-2 {
    margin-left: 20px;
    margin-right: 20px;
  }
}

/* Further adjustments for screens from 800px to 600px */
@media screen and (max-width: 800px) {
  .activities-head{
    font-size: 5.2rem;
  }

  .slider, .slider-reverse {
    max-width: 85%;
    margin: 15px 0;
  }

  .text-activities-1, .text-activities-2, .text-activities-3 {
    max-width: 85%;
    padding: 30px;
    font-size: 15px;
  }

  .text-activities-1 h2, .text-activities-2 h2, .text-activities-3 h2 {
    font-size: 20px;
  }
  .text-activities-1 p, .text-activities-2 p, .text-activities-3 p {
    font-size: 15px;
  }

  /* Swiper image and height adjustments */
  .swiper {
    height: 320px;
  }

  /* Specific fix for slider-reverse and text-activities-2 */
  .slider-reverse {
    padding-top: 50px;
    margin-right: 20px;
  }

  .text-activities-2 {
    margin-left: 20px;
    margin-right: 20px;
  }
}

/* Adjustments for screens between 600px and 400px */
@media screen and (max-width: 600px) {
  .activities-head{
    font-size: 4rem;
  }

  .slider, .slider-reverse {
    max-width: 80%;
    padding-left: 0;
    padding-top: 15px;
    margin: 10px 0;
  }

  .text-activities-1, .text-activities-2, .text-activities-3 {
    max-width: 80%;
    padding: 25px;
    font-size: 14px;
  }

  .text-activities-1 h2, .text-activities-2 h2, .text-activities-3 h2 {
    font-size: 18px;
  }

  .text-activities-1 p, .text-activities-2 p, .text-activities-3 p {
    font-size: 12px;
  }

  /* Fix for smaller resolutions */
  .slider-reverse {
    padding-top: 20px;
    margin-right: 10px;
  }

  .text-activities-2 {
    margin-left: 10px;
    margin-right: 10px;
  }
}

/* Adjustments for screens down to 400px */
@media screen and (max-width: 400px) {
  .activities-head{
    font-size: 3rem;
  }

  .slider, .slider-reverse {
    max-width: 100%;
    margin: 10px 0;
  }

  .text-activities-1, .text-activities-2, .text-activities-3 {
    max-width: 100%;
    padding: 24px;
    font-size: 13px;
  }

  .text-activities-1 h2, .text-activities-2 h2, .text-activities-3 h2 {
    font-size: 16px;
  }

  .text-activities-1 p, .text-activities-2 p, .text-activities-3 p {
    font-size: 10px;
  }

  /* Swiper height adjustments */
  .swiper {
    height: 250px;
  }

  /* Specific fix for slider-reverse */
  .slider-reverse {
    margin: 5px 0;
  }

  .text-activities-2 {
    margin-left: 5px;
    margin-right: 5px;
  }
}
