.news-page {
    position: relative; /* Confines loading screen to this section */
    font-family: 'Poppins';
}

.news-page * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    transition: 100ms;
}

.news-page-body {
    font-family: Arial, sans-serif;
    overflow-x: hidden;
    color: black;
    background-color: #f8f8f8;
}

.news-page-header,
.news-page-footer {
    text-align: center;
    padding: 20px;
    background-color: #333;
    color: white;
}

.news-page-header h1,
.news-page-footer p {
    font-size: 2rem;
    margin-bottom: 10px;
}

.news-page-container {
    padding: 20px;
    max-width: 90vw;
    margin: 0 auto;
}

.news-page-item {
    position: relative;
    height: 90vh;
    margin-bottom: 20px;
    overflow: hidden;
    border-radius: 10px;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    color: white;
    opacity: 0.6; /* Initially hidden for fade-in effect */
    transition: opacity 1s ease; /* Smooth transition for visibility */
}

.news-page-item.news-page-in-view {
    opacity: 1; /* Fade in */
}

.news-page-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
    padding: 20px;
    text-align: center;
}

.news-page-item h2 {
    font-size: 3rem;
    margin-bottom: 10px;
}

.news-page-date {
    font-size: 1.2rem;
    color: #ddd;
    margin-bottom: 10px;
}

.news-page-text {
    font-size: 1.2rem;
    line-height: 1.6;
}

/* Loading Screen Styles */
.news-page-loading-screen {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(219, 219, 219);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    color: #333;
    z-index: 1;
    transition: opacity 0.5s ease;
    opacity: 1;
}

.news-page-loading-screen.hidden {
    opacity: 0;
    display: none;
}

/* Responsive Styles */

/* 4K screens (2560px and up) */
@media (min-width: 2560px) {
    .news-page-header h1,
    .news-page-footer p {
        font-size: 3rem;
    }

    .news-page-item h2 {
        font-size: 4rem;
    }

    .news-page-date,
    .news-page-text {
        font-size: 1.5rem;
    }

    .news-page-loading-screen {
        font-size: 2.5rem;
    }
}

/* Desktops and large laptops (1440px to 2559px) */
@media (min-width: 1440px) and (max-width: 2559px) {
    .news-page-header h1,
    .news-page-footer p {
        font-size: 2.5rem;
    }

    .news-page-item h2 {
        font-size: 3.5rem;
    }

    .news-page-date,
    .news-page-text {
        font-size: 1.4rem;
    }

    .news-page-loading-screen {
        font-size: 2rem;
    }
}

/* Medium-sized screens (laptops, 1024px to 1439px) */
@media (min-width: 1024px) and (max-width: 1439px) {
    .news-page-header h1,
    .news-page-footer p {
        font-size: 2.25rem;
    }

    .news-page-item h2 {
        font-size: 3rem;
    }

    .news-page-item {
        height: 60vh;
    }

    .news-page-date,
    .news-page-text {
        font-size: 1.3rem;
    }
}

/* Tablets (768px to 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
    .news-page-header h1,
    .news-page-footer p {
        font-size: 2rem;
    }

    .news-page-item {
        height: 60vh;
    }

    .news-page-item h2 {
        font-size: 2.5rem;
    }

    .news-page-date,
    .news-page-text {
        font-size: 1.1rem;
    }

    .news-page-loading-screen {
        font-size: 1.8rem;
    }
}

/* Large mobile screens (576px to 767px) */
@media (min-width: 576px) and (max-width: 767px) {
    .news-page-header h1,
    .news-page-footer p {
        font-size: 1.8rem;
    }

    .news-page-item {
        height: 70vh;
    }

    .news-page-item h2 {
        font-size: 2rem;
    }

    .news-page-date,
    .news-page-text {
        font-size: 1rem;
    }

    .news-page-loading-screen {
        font-size: 1.6rem;
    }
}

/* Small mobile screens (400px to 575px) */
@media (min-width: 400px) and (max-width: 575px) {
    .news-page-header h1,
    .news-page-footer p {
        font-size: 1.5rem;
    }

    .news-page-item {
        height: 60vh;
    }

    .news-page-item h2 {
        font-size: 1.8rem;
    }

    .news-page-date,
    .news-page-text {
        font-size: 0.9rem;
    }

    .news-page-loading-screen {
        font-size: 1.4rem;
    }
}

/* Extra small mobile screens (up to 399px) */
@media (max-width: 399px) {
    .news-page-header h1,
    .news-page-footer p {
        font-size: 1.2rem;
    }

    .news-page-item {
        height: 50vh;
    }

    .news-page-item h2 {
        font-size: 1.5rem;
    }

    .news-page-date,
    .news-page-text {
        font-size: 0.8rem;
    }

    .news-page-loading-screen {
        font-size: 1.2rem;
    }
}
