.testimonials {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 100px 0;
    font-family: 'Poppins', sans-serif;
    height: 100%; /* Make the testimonials section cover the entire viewport */
}

.testimonials .container {
    max-width: 100%; /* Increase the container width */
    margin: 0 auto;
    padding: 0 15px;
}

.testimonials .title {
    text-align: center;
    margin-bottom: 50px;
}

.testimonials .title h1 {
    color: #EB6D2F;
    font-size: 6em; /* Increase the font size */
    font-weight: 900;
    text-align: center;
    margin-bottom: 10px;
}

.testimonials .title h2 {
    color: #5A3733;
    font-weight: 900;
    font-size: 50px; /* Increase the font size */
    margin-bottom: 30px;
}

.carousel {
    display: flex;
    justify-content: center;
    position: relative;
    overflow: hidden;
    padding-bottom: 30px;
    padding-top: 0px;
    padding-right: 11px;
}

.carousel-item {
    width: 100%;
    max-width: 1200px;
    height: 100%;
    margin-bottom: 40px;
    opacity: 0;
    transition: opacity 1s ease; /* Add transform for zoom effect */
    position: absolute;
    border: 3px solid #ddd; /* Add border */
    box-shadow: 5px 5px 12px rgb(255, 123, 0); /* Add minimal box-shadow for 3D effect */
    border-radius: 15px; /* Rounded corners */
    padding: 30px;
    background: #fff; /* White background for contrast */
}

.carousel-item.active {
    opacity: 1;
    position: relative;
}

.carousel-item .profile {
    display: flex;
    align-items: center;
    padding: 20px 0;
}

.carousel-item .profile img {
    border-radius: 50%;
    width: 70px; /* Increase the size of the profile image */
    height: 70px;
    object-fit: cover;
}

.carousel-item .information {
    padding-left: 20px;
}

.carousel-item .information p {
    font-size: 24px; /* Larger font size */
    font-weight: bold;
    color: #5A3733;
}

.carousel-item .information span {
    color: #EB6D2F;
    font-size: 16px;
    font-weight: bold;
}

.carousel-item .stars {
    color: #ffd832;
}

.carousel-item p {
    text-align: center;
    font-size: 18px; /* Larger font size */
    font-style: italic;
    color: #5A3733;
}

.carousel-item .icon {
    text-align: center;
    margin-top: 10px;
}

.carousel-item .icon i {
    font-size: 36px; /* Slightly larger icon */
    color: #FFD832;
}

@media only screen and (max-width: 1200px) {
    .testimonials .title h1 {
        font-size: 5rem; /* Adjust font size for medium screens */
    }

    .testimonials .title h2 {
        font-size: 40px; /* Adjust font size for medium screens */
    }
}

@media only screen and (max-width: 800px) {
    .testimonials .title h1 {
        font-size: 4rem; /* Adjust title size for smaller screens */
    }

    .testimonials .title h2 {
        font-size: 30px; /* Adjust subtitle size for smaller screens */
    }

    .carousel-item {
        margin-bottom: 20px; /* Reduce margin for carousel items */
        padding: 20px; /* Reduce padding for carousel items */
    }

    .carousel-item .profile img {
        width: 50px; /* Reduce image size */
        height: 50px;
    }

    .carousel-item .information p {
        font-size: 20px; /* Adjust font size for smaller screens */
    }

    .carousel-item p {
        font-size: 16px; /* Adjust font size for smaller screens */
    }

    .carousel-item .icon i {
        font-size: 28px; /* Smaller icon size for mobile */
    }
}

@media only screen and (max-width: 600px) {
    .testimonials .title h1 {
        font-size: 3rem; /* Adjust title size for mobile */
    }

    .testimonials .title h2 {
        font-size: 24px; /* Adjust subtitle size for mobile */
    }

    .carousel-item .profile img {
        width: 40px; /* Further reduce image size */
        height: 40px;
    }

    .carousel-item .information p {
        font-size: 18px; /* Smaller font size for text */
    }

    .carousel-item p {
        font-size: 14px; /* Smaller font size for text */
    }

    .carousel-item .icon i {
        font-size: 24px; /* Further reduce icon size */
    }
}

@media only screen and (max-width: 400px) {
    .testimonials .title h1 {
        font-size: 2.5rem; /* Adjust title size for very small screens */
        padding-bottom: 20px;
    }

    .testimonials .title h2 {
        font-size: 18px; /* Adjust subtitle size for very small screens */
    }

    .carousel-item {
        margin-bottom: 10px; /* Further reduce margin */
        padding: 15px; /* Further reduce padding */
    }

    .carousel-item .profile img {
        width: 35px; /* Smallest image size for mobile */
        height: 35px;
    }

    .carousel-item .information p {
        font-size: 16px; /* Smaller font size */
    }

    .carousel-item p {
        font-size: 12px; /* Smaller font size for testimonials */
    }

    .carousel-item .icon i {
        font-size: 20px; /* Smaller icon size for mobile */
    }
}
