/* Menu Bar Styling */
.menu-bar {
  background: linear-gradient(-45deg, #c12d00, #a4003f, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  top: 0;
  z-index: 900;
  padding: 20px 0;
  width: 100%;
  transition: opacity 0.5s ease, background-size 0.5s ease;
  opacity: 1;
}

.menu-bar.hidden {
  top: -100px; /* Move it out of the viewport */
}

.menu-bar.sticky {
  position: sticky;
  top: 0;
  z-index: 900;
}

.menu-bar.translucent {
  opacity: 0.6;
}

/* Menu Links Container */
.menu-links {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
  margin: 0;
  padding: 0;
  flex-wrap: wrap; /* Prevents overflow */
}

/* Each Menu Item */
.menu-links li {
  display: inline;
}

/* Default Menu Link Styling */
.menu-links a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 18px;
  padding: 10px 20px;
  position: relative;
  transition: color 0.3s ease, background-color 0.3s ease;
}

/* Active Menu Link - LED-like Color Animation */
.menu-links a.active {
  position: relative;
  background: linear-gradient(to right, #000ea8, #0015fe, #4c0093, #3c009c);
  background-size: 400% 400%;
  animation: activeGradient 5s ease infinite;
  color: white;
  padding: 10px 20px;

  /* Animated glowing border */
  border: solid 3px;
  border-radius: 10px;
}

.menu-links a.active::after {
  filter: blur(10px);
  transform: translate3d(0, 0, 0); /* For Safari */
}

/* Hover and Transition Styling */
.menu-links a:hover {
  transition: 400ms;
  color: #b4ffe8;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
}

/* Smooth Directional Transition */
.menu-links a {
  transition: background-color 0.5s ease, color 0.5s ease;
}

.menu-links a.active {
  /* For smooth directional transition */
  transition: background-color 0.4s ease-out, transform 0.4s ease;
  transform: scale(1.1);
}

.menu-links a:not(.active):hover {
  transform: translateX(10px);
}


/* Keyframes for animations */
@keyframes glow {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 100% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes activeGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Responsive Design */

@media only screen and (max-width: 1100px){
  .menu-links a{
    font-size: 15px;
    padding: 8px 15px;
  }
}

@media (max-width: 950px){
  .menu-bar.sticky{
    z-index: 10;
  }
}

/* For screen widths 768px and below */
@media (max-width: 900px) {
  .menu-bar {
    padding: 15px 0;
    background-size: 300% 300%; /* Reduce background size */
  }

  .menu-links a {
    font-size: 12px; /* Decrease font size */
    padding: 8px 12px; /* Reduce padding */
  }

  .menu-bar.sticky{
    position: sticky;
    z-index: 10;
  }
}

/* For screen widths 600px and below */
@media (max-width: 700px) {
  .menu-bar {
    padding: 15px 0;
    background-size: 300% 300%; /* Further reduce background size */
  }

  .menu-links {
    justify-content: space-evenly;
  }

  .menu-links a {
    font-size: 10px; /* Further decrease font size */
    padding: 10px 8px; /* Adjust padding */
  }
}

@media (max-width: 768px){
  .menu-bar{
    display: none;
  }
}

/* For screen widths 480px and below */
@media (max-width: 480px) {
  .menu-bar {
    padding: 8px 0;
    background-size: 200% 200%; /* Reduce background size more */
  }

  .menu-links {
    flex-direction: column; /* Stack menu items vertically */
    align-items: center;
  }

  .menu-links a {
    font-size: 12px; /* Smaller font for mobile screens */
    padding: 6px 10px; /* Further reduce padding */
    width: 100%; /* Make links take full width */
    text-align: center;
  }
}