/* POPPINS FONT */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

.teacher-profile-container {
  /* margin-top: 130px; */
  display: flex;
  justify-content: center;
  align-items: flex-start;
  /* Changed to align cards at the top */
  flex-wrap: wrap;
  /* margin-bottom: -150px; */
  font-family: 'Poppins';
}

.teacher-profile-card-container {
  position: relative;
  width: calc(50% - 200px);
  /* 3 cards per row with margins */
  height: 500px;
  background: #fcebff;
  margin: 10px;
  /* Adjusted margin for spacing */
  border-radius: 30px;
  box-shadow: 1px 5px 10px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  transition: transform 0.3s ease;
}

.teacher-profile-card-container:hover {
  transform: scale(1.05);
}

.teacher-profile-top-box {
  background: #ff7575;
  height: 120px;
  border-radius: 30px;
  padding: 20px 30px;
}

.teacher-profile-top-menu {
  display: flex;
  justify-content: space-between;
}

.teacher-profile-top-box i {
  font-size: 22px;
}

.teacher-profile-dropdown {
  position: relative;
  display: inline-block;
}

.teacher-profile-dropbtn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 40px;
  background: transparent;
  border: none;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.teacher-profile-dropdown-content {
  display: none;
  position: absolute;
  width: 180px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

/* Show the dropdown on hover */
.teacher-profile-dropdown:hover .teacher-profile-dropdown-content {
  display: block;
}

.teacher-profile-dropdown:hover .teacher-profile-dropbtn {
  box-shadow: inset 0px 0px 2px 1px #3845a1;
}

.teacher-profile-menu-list {
  display: flex;
  flex-direction: column;
}

.teacher-profile-menu-list a,
.teacher-profile-menu-list button {
  text-decoration: none;
  font-size: 15px;
  color: #000;
  padding: 10px 20px;
}

.teacher-profile-menu-list button {
  background-color: white;
  border: none;
  text-align: left;
  font-size: 15px;
  font-family: 'Poppins';
}

.teacher-profile-menu-list a:hover,
.teacher-profile-menu-list button:hover {
  background: #ffc9e2;
  cursor: pointer;
}

.teacher-profile-more {
  right: 0;
  padding: 10px;
}

.teacher-profile-skills-title {
  font-size: 15px;
  color: #ee2482;
}

.teacher-profile-skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  margin: 5px;
}

.teacher-profile-skills-list label {
  font-size: 13px;
  background: #7760e6;
  color: #fff;
  padding: 1px 8px;
  border-radius: 3px;
}

/* Image Box */
.teacher-profile-image-box {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: -125px auto 10px auto;
  height: 240px;
  width: 240px;
  background: #ffc1c1;
  border-radius: 50%;
}

.teacher-profile-image-box img {
  width: 225px;
  border-radius: 50%;
  box-shadow: 0px 3px 2px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}

.teacher-profile-image-box img:hover {
  transform: scale(1.1);
  cursor: pointer;
}

/* User Information */
.teacher-profile-user-info .teacher-profile-name {
  display: flex;
  justify-content: center;
  font-weight: 600;
  font-size: 28px;
  padding-bottom: 10px;
}

.teacher-profile-user-info .teacher-profile-job {
  display: flex;
  justify-content: center;
  font-size: 20px;
  color: #ee2482;
  padding-bottom: 10px;
}

/* Social icons */
.teacher-profile-social-icons {
  display: flex;
  justify-content: center;
  margin: 10px 0 20px 0;
}

.teacher-profile-social-icon-box {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.teacher-profile-social-icon-box span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  border: 1px solid #593ceb;
  margin: 5px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.4s;
}

.teacher-profile-social-icon-box span:hover{
  color: #fff;
  box-shadow: inset 0 0 0 20px #8c78f0;
  transform: translateY(-5px);
}

.svg-icon:hover{
  fill: #ffffff;
}

.teacher-profile-social-icon-box .teacher-profile-social-icon-name {
  position: absolute;
  top: 0;
  opacity: 0;
  padding: 5px 20px;
  font-size: 13px;
  background: #8c78f0;
  color: #fff;
  border-radius: 30px;
  transition: all 0.3s cubic-bezier(0.07, -0.55, 0.265, 1.55);
}

.teacher-profile-social-icon-box span:hover~.teacher-profile-social-icon-name {
  top: -45px;
  opacity: 1;
}

/* Animated circles */
.teacher-profile-circle-1 {
  position: absolute;
  left: -20px;
  top: 35%;
  height: 50px;
  width: 50px;
  border: 2px dashed #c975fb;
  border-radius: 50%;
  animation: teacher-profile-circle-1 8s ease-in-out infinite;
}

.teacher-profile-circle-2 {
  position: absolute;
  right: -20px;
  bottom: -30px;
  height: 100px;
  width: 100px;
  border: 2px dashed #c975fb;
  border-radius: 50%;
  animation: teacher-profile-circle-2 15s ease-in-out infinite;
}

/* Circle-1 animation */
@keyframes teacher-profile-circle-1 {
  50% {
    left: -20px;
    top: 50%;
  }
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }

  0% {
    transform: rotate(-360deg);
  }
}

/* Circle-2 animation */
@keyframes teacher-profile-circle-2 {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.teacher-profile-full-image-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.teacher-profile-full-image {
  max-width: 90%;
  max-height: 90%;
  border-radius: 10px;
}

@media (max-width: 1400px){
  .teacher-profile-user-info .teacher-profile-name{
    font-size: 24px;
  }
}
@media (max-width: 1200px){
  .teacher-profile-user-info .teacher-profile-name{
    font-size: 20px;
  }
  .teacher-profile-card-container {
    width: calc(50% - 50px);
  }
}

@media (max-width: 1000px) and (max-height: 2000px){
  .teacher-profile-user-info .teacher-profile-name{
    font-size: 18px;
  }
}

@media (max-width: 980px) {
  .teacher-profile-card-container {
    width: calc(50% - 30px);
    /* 2 cards per row on smaller screens */
  }
  .teacher-profile-card-container:nth-child(2){
    margin-bottom: 50px;
  }

  .teacher-profile-user-info .teacher-profile-name{
    font-size: 20px;
  }
}

@media (max-width: 700px) {
  .teacher-profile-card-container {
    width: calc(100% - 40px);
    /* 1 card per row on very small screens */
  }

  .teacher-profile-card-container:nth-child(2){
    margin-bottom: 50px;
  }
}

.teacher-profile-popup-no-border {
  border: 0;
  border-radius: 50%;
}
