.publications-container {
    padding: 20px;
    text-align: center;
}

.publications-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.publication-item {
    width: 300px;
    margin: 10px;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
}

.publication-item h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
}

.publication-item p {
    font-size: 1rem;
    margin-bottom: 10px;
}

.publication-item a {
    font-weight: bold;
    color: #007bff;
    text-decoration: none;
}

.publication-item a:hover {
    text-decoration: underline;
}

.error {
    color: red;
}
