/* Resource.css */

.resource-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    text-align: justify;
    font-family: 'Poppins', sans-serif;
    overflow-x: hidden; /* Prevent overflow in x direction */
}

.video-container {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    overflow: hidden; /* Contain any overflow */
    width: 100%;
}

.video-container iframe,
.video-container video {
    max-width: 100%; /* Ensure video fits within container */
    height: auto;
}

.button-container {
    text-align: center;
    margin: 20px 0;
}

.pdf-download-btn {
    display: inline-block;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #007bff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    max-width: 90%; /* Prevent button overflow */
    overflow: hidden; /* Contain text if necessary */
    white-space: nowrap; /* Prevent wrapping if desired */
}

.pdf-download-btn:hover {
    background-color: #0056b3;
}

.image-container {
    display: flex;
    justify-content: center;
    margin: 20px 0;
}

.image-container img {
    max-width: 100%;
    height: auto;
}

.text-section {
    margin: 30px 0;
}

.text-section h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #007bff;
}

.text-section p,
.text-section ul {
    margin: 10px 0 20px 0;
    line-height: 1.6;
    color: #555;
}

.text-section ul {
    padding-left: 20px;
}

.text-section li {
    margin-bottom: 10px;
}

/* Responsive Design */

@media (max-width: 768px) {
    .video-container iframe,
    .video-container video {
        width: 100%;
        height: auto;
    }

    .pdf-download-btn {
        width: 100%;
        padding: 15px;
    }

    .strategic-text {
        font-size: 1rem;
    }

    .text-section h2 {
        font-size: 1.3rem;
    }
}

/* Very small screens (200px to 300px) */
@media (max-width: 300px) {
    .resource-container {
        padding: 10px;
    }

    .pdf-download-btn {
        padding: 10px;
        font-size: 0.8rem;
    }

    .strategic-text {
        font-size: 0.8rem;
    }

    .text-section h2 {
        font-size: 1.1rem;
    }

    .text-section p, .text-section ul {
        font-size: 0.9rem;
    }
}
