@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

.wrapper-count {
    background: -webkit-linear-gradient(rgb(255, 221, 232) 40%, rgb(221, 236, 251) 80%);
    background-size: cover;
    background-position: center;
    font-family: "Poppins", sans-serif;
    width: 100%;
    display: flex;
    justify-content: space-around;
    gap: 10px;
    margin: 0 auto; /* Center align the wrapper-count */
    padding: 50px 0;
}

.container-count {
    width: 32vmin;
    height: 28vmin;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 1em 0;
    font-size: 21px;
    border-radius: 0.5em;
    border-bottom: 5px solid transparent; /* Ensure border doesn't extend */
    border-image: linear-gradient(to right, #a283d8, #d53a9d) 1; /* Gradient for the border */
    position: relative; /* Ensure children stay within container */
    overflow: hidden; /* Prevent content overflow */
    box-sizing: border-box; /* Ensure padding and border are included in width/height */
}

.icon {
    color: #d53a9d;
    font-size: 30px;
    text-align: center;
}

span.num {
    display: grid;
    place-items: center;
    font-weight: 600;
    font-size: 3em;
}

span.text {
    font-size: 1em;
    text-align: center;
    padding: 0.7em 0;
    font-weight: 400;
}

/* MEDIA QUERIES */

@media screen and (max-width: 1024px) {
    .wrapper-count {
        width: 100%;
    }
    .container-count {
        width: calc(33.33% - 40px); /* Set flexible width */
        height: 30vmin;
        font-size: 16px; /* Adjust font size */
    }
}

@media screen and (max-width: 768px) {
    .wrapper-count {
        width: 100%;
        flex-wrap: wrap;
        gap: 30px;
    }
    .container-count {
        width: calc(50% - 40px); /* Set flexible width */
        height: 30vmin;
        font-size: 14px; /* Adjust font size */
        border-bottom-width: 4px; /* Decrease border width */
    }
}

@media screen and (max-width: 480px) {
    .wrapper-count {
        gap: 15px;
    }
    .container-count {
        width: 60%;
        height: auto; /* Use auto to avoid overflow on small screens */
        font-size: 12px;
        padding: 10px 0; /* Adjust padding to make content fit */
        border-bottom-width: 4px;
    }
}

@media (max-height: 450px){
    .container-count{
        height: 50vmin;
    }
}
