@import url('https://fonts.cdnfonts.com/css/ica-rubrik-black');
@import url('https://fonts.cdnfonts.com/css/poppins');

.team-container {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #fff8f8;
}

.team-head{
    font-family: 'ICA Rubrik';
    font-size: 10em;
    line-height: 1em;
    color: #25283B;
    position: relative;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.banner{
    /* margin-top: 100px; */
    width: 100%;
    height: 140vh;
    text-align: center;
    overflow: hidden;
    position: relative;
}
.banner .slider{
    position: absolute;
    width: 200px;
    height: 350px;
    top: 10%;
    left: calc(50% - 150px);
    transform-style: preserve-3d;
    transform: perspective(1000px);
    animation: autoRun 100s linear infinite;
    z-index: 2;
}
@keyframes autoRun{
    from{
        transform: perspective(1000px) rotateX(-11deg) rotateY(0deg);
    }to{
        transform: perspective(1000px) rotateX(-11deg) rotateY(360deg);
    }
}

.banner .slider .item{
    position: absolute;
    inset: 0 0 0 0;
    transform: 
        rotateY(calc( (var(--position) - 1) * (360 / var(--quantity)) * 1deg))
        translateZ(550px);
}
.banner .slider .item img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
}
.banner .content{
    position: absolute;
    bottom: 0;
    transform: translateX(3%);
    width: min(1400px, 100vw);
    height: max-content;
    padding-bottom: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    z-index: 1;
}
.banner .content h1{
    font-family: 'ICA Rubrik';
    font-size: 16em;
    line-height: 1em;
    color: #25283B;
    position: relative;
    text-align: center;
}
.banner .content h1::after{
    position: absolute;
    inset: 0 0 0 0;
    content: attr(data-content);
    z-index: 2;
    -webkit-text-stroke: 2px #d2d2d2;
    color: transparent;
}
.banner .content .author{
    font-family: Poppins;
    text-align: right;
    max-width: 200px;
}
.banner .content h2{
    font-size: 3em;
}
.banner .content .model{
    width: 100%;
    height: 75vh;
    position: absolute;
    bottom: 0;
    left: 0;
    background-size: auto 130%;
    background-repeat: no-repeat;
    background-position: top center;
    z-index: 1;
}

@media (max-width: 1708px) and (min-height: 1200px){
    .banner{
        height: 100vh;
    }
}

