/* Navbar styling */
.navbar {
  background: url("../../public/Abstract_technology_of_science_background_generated.jpg") no-repeat center center;
  background-size: cover;
  padding: 1rem;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Styling for the navbar logo */
.navbar-logo {
  width: 250px; /* Default width for desktop */
}

.bcrec-logo {
  width: 100px; /* Default width for desktop */
  position: relative;
  float: right;
}

/* Admin link styling */
.admin-link {
  color: #000;
  text-decoration: none;
  font-size: 1.5rem;
  font-family: 'Poppins';
  padding: 0.5rem 1rem;
  border: 2px solid transparent;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.admin-link:hover {
  color: #c90000;
  background-color: rgba(69, 255, 224, 0.2);
  border-color: #f92525;
  transform: scale(2);
}

/* Navbar menu styling */
.nav-menu {
  position: relative;
  list-style: none;
  display: flex;
  align-items: center;
}

.nav-menu li {
  margin-left: 1.5rem;
}

/* Responsive Design */

@media (max-width: 1200px){
  .admin-link {
    display: none; 
  }
}

/* For screen widths of 700px or less */
@media (max-width: 768px) {
  .admin-link {
    display: none; /* Hide admin link on smaller screens */
  }

  .navbar-logo{
    width: 180px;
  }
}

/* For screen widths of 600px or less */
@media (max-width: 600px) {
  .navbar {
    padding: 0.8rem;
    background-size: cover;
  }

  .navbar-logo {
    width: 180px; /* Reduce logo width for smaller screens */
  }

  .bcrec-logo {
    width: 80px; /* Reduce secondary logo size */
  }

  .nav-menu {
    display: block; /* Stack the menu vertically */
  }

  .nav-menu li {
    margin-left: 0;
  }

  .admin-link {
    font-size: 1rem; /* Adjust font size for mobile */
  }
}

/* For screen widths of 500px or less */
@media (max-width: 500px) {
  .navbar {
    flex-direction: column; /* Stack elements vertically */
    text-align: center; /* Center-align content */
  }

  .bcrec-logo {
    float: none; /* Remove float for better stacking */
    text-align: center;
  }

  .navbar-logo {
    width: 150px; /* Further reduce logo size */
  }

  .bcrec-logo {
    width: 70px; /* Further reduce secondary logo size */
  }

  .admin-link {
    font-size: 0.9rem; /* Adjust font size for smaller screens */
  }
}

@media screen and (max-width: 300px) {
  .navbar-logo {
    width: 100px; /* Further reduce logo size */
  }

  .bcrec-logo {
    width: 50px; /* Further reduce secondary logo size */
  }

  .admin-link {
    font-size: 0.8rem; /* Adjust font size for the smallest screens */
  }
}

