@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
    margin: 0;
    padding: 0;
}

.about-container{
    position: relative;
}

.about-heading {
    padding-top: 0;
    margin-top: 0;
    font-size: 3rem;
    padding-bottom: 15px;
    font-weight: 600;
}

.about {
    background: -webkit-linear-gradient(rgb(221, 236, 251) 40%, rgb(255, 221, 232) 80%);
    background-color: rgba(240, 174, 255, 0.6);
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-style: normal;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}

.word {
    flex: 1;
    margin-left: 20px;
    min-width: 250px;
}

.about-para {
    margin-right: 20px;
    font-size: 1.2rem;
    text-align: justify;
}

.about-list{
    padding-top: 10px;
    padding-left: 20px;
    padding-bottom: 20px;
}

.about-video {
    flex: 1;
    margin-left: 20px;
    max-width: 100%;
    min-width: 250px;
    border-radius: 10px;
    height: auto;
    aspect-ratio: 16/10;
}

/* Media Queries for Responsive Design */

/* Mobile devices */
@media only screen and (max-width: 1000px) {
    .about {
        flex-direction: column;
        align-items: flex-start;
    }

    .about-heading{
        text-align: center;
        margin: 0;
        margin-left: -40px;
    }

    .about-para{
        padding: 0 20px;
    }

    .about-video {
        margin-left: 50px;
        margin-top: 50px;
        width: 90%;
        height: auto;
    }

    .about-para {
        font-size: 1rem;
        margin: 0;
        margin-left: -20px;
    }
}

/* Tablet devices (landscape) */
@media only screen and (max-width: 850px) {
    .about-heading {
        font-size: 2rem;
        /* text-align: center; */
    }

    .about-para {
        font-size: 0.9rem;
    }

    .about-video {
        margin-top: 40px;
        margin-left: auto;
        margin-right: auto;
        display: block;
        align-items: center;
        justify-items: center;
        height: auto;
    }
}