.projects-container {
    padding: 2rem;
    background-color: #f9f9f9;
    text-align: center;
    position: relative;
}

.projects-heading {
    font-size: 3rem;
    margin-bottom: 2rem;
    color: #333;
}

.projects-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 4rem;
    justify-content: center;
}

.project-card {
    background-color: #fff;
    padding: 2rem;
    border-radius: 12px;
    box-shadow: 5px 6px 10px rgba(255, 90, 90, 0.737);
    width: 400px;
    text-align: center;
}

.project-image {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius: 8px;
    margin-bottom: 1.5rem;
}

.project-card h2 {
    font-size: 2rem;
    margin-bottom: 0.75rem;
    color: #333;
}

.project-card p {
    font-size: 1.2rem;
    color: #666;
}

.project-link {
    margin-top: 1.5rem;
    background-color: #007bff;
    color: white;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1.1rem;
}

.project-link:hover {
    background-color: #0056b3;
}

/* Work in Progress section */
.work-in-progress {
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.gear-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.small-gear {
    font-size: 2.5rem;
    margin-right: -4px;
    margin-top: 10px;
    color: #8b8b8b;
    animation: rotate-opposite 3s infinite linear;
}

.large-gear {
    font-size: 5rem;
    color: #bdb99e;
    animation: rotate 3s infinite linear;
}

.work-in-progress p {
    font-size: 1.8rem;
    color: #333;
    margin-top: 1rem;
}

/* Rotating gear animation */
@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes rotate-opposite {
    100% {
        transform: rotate(-360deg);
    }
}