/* Main menu positionning */
.mobile-menu-main-nav {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    text-align: center;
    height: 100%;
    background: transparent;
    opacity: 0;
    z-index: 10;
    visibility: hidden;
    transition: all .375s;
}

.no-scroll {
    overflow: hidden;
    /* Disable scrolling on the body */
}

.mobile-menu-main-nav.is-open {
    opacity: 1;
    z-index: 100;
    visibility: visible;
}

/* Yellow band effect */
.mobile-menu-main-nav::before {
    content: '';
    position: fixed;
    top: 0px;
    bottom: 0px;
    left: 0;
    right: -20px;
    background: #fedb2ae8;
    transform-origin: 0 0;
    transform: skew(-12deg) translateX(-100%);
    transition: all .275s .1s;
}

.mobile-menu-main-nav.is-open::before {
    transform: skew(-9deg) translateX(-120px);
}

/* Skewing effect on menu links */
.mobile-menu-main-nav ul {
    display: inline-flex;
    flex-direction: column;
    height: 100%;
    /* Should be 100%, but we have a notice message :D */
    align-items: flex-end;
    justify-content: center;
    transform: translateX(100px) skew(-9deg);
}

.mobile-menu-main-nav li {
    display: block;
    margin: .5rem 0;
    text-align: right;
    transform: skew(6deg);
    font-size: 20px;
}

/* Apparition effect on links */
.mobile-menu-main-nav a,
.mobile-menu-main-nav button {
    /* Add styling for button */
    opacity: 0;
    transform: translateY(-10px);
}

.mobile-menu-links a.active,
.mobile-menu-links button.active {
    /* Make button 'active' the same as links */
    color: rgb(255, 104, 104);
}

.mobile-menu-main-nav.is-open a,
.mobile-menu-main-nav.is-open button {
    /* Button and links when the menu is open */
    opacity: 1;
    transform: translateX(0px) translateY(-70px);
}

.mobile-menu-main-nav li:nth-child(1) a,
.mobile-menu-main-nav li:nth-child(1) button {
    transition: all 275ms 175ms;
}

.mobile-menu-main-nav li:nth-child(2) a,
.mobile-menu-main-nav li:nth-child(2) button {
    transition: all 275ms 225ms;
}

.mobile-menu-main-nav li:nth-child(3) a,
.mobile-menu-main-nav li:nth-child(3) button {
    transition: all 275ms 275ms;
}

.mobile-menu-main-nav li:nth-child(4) a,
.mobile-menu-main-nav li:nth-child(4) button {
    transition: all 275ms 325ms;
}

.mobile-menu-main-nav li:nth-child(5) a,
.mobile-menu-main-nav li:nth-child(5) button {
    transition: all 275ms 375ms;
}

.mobile-menu-main-nav li:nth-child(6) a,
.mobile-menu-main-nav li:nth-child(6) button {
    transition: all 275ms 375ms;
}

.mobile-menu-main-nav li:nth-child(7) a,
.mobile-menu-main-nav li:nth-child(7) button {
    transition: all 275ms 375ms;
}

.mobile-menu-main-nav li:nth-child(8) a,
.mobile-menu-main-nav li:nth-child(8) button {
    transition: all 275ms 375ms;
}

.mobile-menu-main-nav li:nth-child(9) a,
.mobile-menu-main-nav li:nth-child(9) button {
    transition: all 275ms 375ms;
}

/* Decoration */
.mobile-menu-main-nav ul,
.mobile-menu-main-nav li {
    list-style: none;
    padding: 0;
}

.mobile-menu-main-nav a,
.mobile-menu-main-nav button {
    /* Apply the same decoration to button */
    display: block;
    padding: 12px 0;
    color: #5A3B5D;
    font-size: 1.4em;
    text-decoration: none;
    font-weight: bold;
    background: none;
    /* Remove background */
    border: none;
    /* Remove border */
    cursor: pointer;
    /* Make the button clickable */
}

.mobile-menu-open-main-nav {
    position: absolute;
    top: 15px;
    /* Adjust as needed */
    left: 50%;
    /* Center horizontally */
    transform: translateX(-50%);
    /* Offset by half of its own width */
    z-index: 1000;
    background: none;
    border: 0;
    cursor: pointer;
    display: flex;
    /* Center contents */
    justify-content: center;
    /* Horizontally center */
}

.mobile-menu-open-main-nav:focus {
    outline: none;
}

.mobile-menu-burger {
    position: relative;
    display: block;
    text-align: center;
    justify-items: center;
    align-items: center;
    width: 28px;
    height: 4px;
    margin: 0 auto;
    /* background: #5A3B5D; */
    transform: skew(5deg);
    transition: all .275s;
}

.mobile-menu-burger:after,
.mobile-menu-burger:before {
    content: '';
    display: block;
    height: 100%;
    /* background: #5A3B5D; */
    transition: all .275s;
}

.mobile-menu-burger-icon {
    font-size: 30px;
    transition: transform 300ms ease-in-out;
    transform: rotate(0deg);
}

button:focus,
button:active,
a:focus,
a:active,
.mobile-menu-burger-icon:focus,
.mobile-menu-burger-icon:active{
    outline: none;
    box-shadow: none; /* Remove any box shadow that might create an outline effect */
}

.mobile-menu-burger-icon.rotate-360 {
    animation: rotate360 1s ease-in-out forwards;
    transition: transform 300ms ease-in-out;
}

/* Keyframe for 360-degree rotation */
@keyframes rotate360 {
    from {
        transform: rotate(-180deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.mobile-menu-open-main-nav.is-open .mobile-menu-burger-icon {
    transition: transform 300ms ease-in-out;
    /* transform: rotate(180deg); */
    animation: rotate180 300ms ease-out backwards;
    transform: rotate(180deg);
}

@keyframes rotate180{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(180deg);
    }
}

.mobile-menu-burger:after {
    transform: translateY(-12px) translateX(-2px) skew(-20deg);
}

.mobile-menu-burger:before {
    transform: translateY(-16px) skew(-10deg);
}

/* Toggle State part */
.is-open .mobile-menu-burger {
    transform: skew(5deg) translateY(-8px) rotate(-45deg);
}

.is-open .mobile-menu-burger:before {
    transform: translateY(0px) skew(-10deg) rotate(75deg);
}

.is-open .mobile-menu-burger:after {
    transform: translateY(-12px) translateX(10px) skew(-20deg);
    opacity: 0;
}

/* MENU Text part */

.mobile-menu-burger-text {
    display: block;
    font-size: .675rem;
    letter-spacing: .05em;
    margin-top: .5em;
    text-transform: uppercase;
    font-weight: 500;
    text-align: center;
    color: #5A3B5D;
}

.mobile-menu-container {
    position: absolute;
    top: 0px;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    /* background: linear-gradient(to bottom, #eee, #ddd); */
    opacity: 0;
}

@media (max-width: 768px) {
    .mobile-menu-container {
        opacity: 1;
    }

    .mobile-menu-main-nav.is-open::before {
        transform: skew(-7deg) translateX(-80px);
    }

    .mobile-menu-main-nav ul {
        transform: translateX(10px) skew(-7deg)translateY(70px);
    }

    .mobile-menu-main-nav li {
        font-size: 16px;
    }

    .mobile-menu-burger-icon {
        margin-top: 30px;
    }
}

