/* Main menu positioning */
.ieee-sites-main-nav {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    background: transparent;
    opacity: 0;
    z-index: 1000;
    visibility: hidden;
    transition: opacity 0s ease-in-out, visibility 0.3s ease-in-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px)translateX(100%);
        /* Optional: Slide down slightly */
    }

    to {
        opacity: 1;
        transform: translateY(0)translateX(0);
    }
}

.ieee-sites-main-nav.fade-in {
    animation: fadeIn 1s ease forwards;
    /* Apply fade-in on initial mount */
    opacity: 1;
    visibility: visible;
}

/* Set open state with animation */
.ieee-sites-main-nav.is-open {
    opacity: 1;
    visibility: visible;
}

.ieee-sites-main-nav.is-open {
    opacity: 1;
    z-index: 100;
    visibility: visible;
}

/* Yellow band effect */
.ieee-sites-main-nav::before {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: -15px;
    background: #000000c0;
    transform-origin: 0 0;
    transform: skew(12deg) translateX(100%);
    transition: all .275s .1s;
    animation: bgrd 100ms ease-in forwards;
}

@keyframes bgrd {
    from {
        opacity: 0;
        transform: translateX(5000px)translateY();
    }

    to {
        opacity: 1;
        transform: translateX(0)translateY();
    }
}

.ieee-sites-main-nav.is-open::before {
    transform: skew(9deg) translateX(120px);
}

/* Skewing effect on menu links */
.ieee-sites-main-nav ul {
    display: inline-flex;
    flex-direction: column;
    height: 100%;
    align-items: flex-start;
    justify-content: center;
    transform: translateX(-100px) skew(9deg);
}

.ieee-sites-main-nav li {
    display: block;
    margin: 1rem 0;
    text-align: left;
    transform: skew(-6deg);
    font-size: 20px;
}

/* Apparition effect on links */
.ieee-sites-main-nav a {
    opacity: 0;
    transform: translateY(10px);
    margin: 0;
}

.ieee-sites-links a.active {
    color: rgb(104, 255, 159);
}

.ieee-sites-main-nav.is-open a {
    opacity: 1;
    transform: translateX(0px) translateY(70px);
}

.ieee-sites-main-nav li:nth-child(1) a {
    transition: all 275ms 175ms;
    animation: links 175ms ease-in forwards;
}

.ieee-sites-main-nav li:nth-child(2) a {
    transition: all 275ms 225ms;
    animation: links 225ms ease-in forwards;
}

.ieee-sites-main-nav li:nth-child(3) a {
    transition: all 275ms 275ms;
    animation: links 275ms ease-in forwards;
}

.ieee-sites-main-nav li:nth-child(4) a {
    transition: all 275ms 325ms;
    animation: links 325ms ease-in forwards;
}

.ieee-sites-main-nav li:nth-child(5) a {
    transition: all 275ms 375ms;
    animation: links 375ms ease-in forwards;
}

@keyframes links {
    from {
        opacity: 0;
        transform: translateX(300px) translateY(200px);
    }

    to {
        opacity: 1;
        transform: translateX(0px) translateY();
    }
}

/* Decoration */
.ieee-sites-main-nav ul,
.ieee-sites-main-nav li {
    list-style: none;
    padding: 20px;
}

.ieee-sites-main-nav a {
    display: block;
    padding: 12px 0;
    color: #ffffff;
    font-size: 1.4em;
    text-decoration: none;
    font-weight: bold;
}

.ieee-sites-open-main-nav {
    position: absolute;
    top: 15px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    background: none;
    border: 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
}

.ieee-sites-open-main-nav:focus {
    outline: none;
}

.ieee-sites-burger {
    position: relative;
    display: block;
    text-align: center;
    width: 28px;
    height: 4px;
    margin: 0 auto;
    background: #ffffff;
    transform: skew(-5deg);
    transition: all .275s;
}

.ieee-sites-burger:after,
.ieee-sites-burger:before {
    content: '';
    display: block;
    height: 100%;
    background: #ffffff;
    transition: all .275s;
}

.ieee-sites-burger-icon {
    font-size: 30px;
}

.ieee-sites-burger:after {
    transform: translateY(12px) translateX(2px) skew(20deg);
}

.ieee-sites-burger:before {
    transform: translateY(16px) skew(10deg);
}

/* Toggle State part */
.is-open .ieee-sites-burger {
    transform: skew(-5deg) translateY(8px) rotate(45deg);
}

.is-open .ieee-sites-burger:before {
    transform: translateY(0px) skew(10deg) rotate(-75deg);
}

.is-open .ieee-sites-burger:after {
    transform: translateY(12px) translateX(-10px) skew(20deg);
    opacity: 0;
}

/* MENU Text part */
.ieee-sites-burger-text {
    display: block;
    font-size: .675rem;
    letter-spacing: .05em;
    margin-top: .5em;
    text-transform: uppercase;
    font-weight: 500;
    text-align: center;
    color: #3B5A5D;
}

.ieee-sites-container {
    position: absolute;
    top: 0px;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: hidden;
    opacity: 0;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .ieee-sites-container {
        opacity: 1;
    }

    .ieee-sites-main-nav.is-open::before {
        transform: skew(7deg) translateX(80px);
    }

    .ieee-sites-main-nav ul {
        transform: translateX(0px) skew(7deg) translateY(-70px);
    }

    .ieee-sites-main-nav li {
        font-size: 16px;
    }

    .ieee-sites-burger-icon {
        margin-top: 30px;
    }
}

@media (max-width: 600px) {
    .ieee-sites-main-nav.is-open::before {
        transform: skew(7deg) translateX(80px);
    }

    .ieee-sites-main-nav ul {
        transform: translateX(80px) skew(7deg) translateY(-50px);
    }

    .ieee-sites-main-nav li {
        font-size: 15px;
    }
}

@media (max-width: 550px) and (max-height: 730px) {
    .ieee-sites-main-nav.is-open::before {
        transform: skew(7deg) translateX(80px);
    }

    .ieee-sites-main-nav ul {
        transform: translateX(30px) skew(7deg) translateY(-70px);
    }

    .ieee-sites-main-nav li {
        font-size: 15px;
    }
}

@media (max-width: 500px) {
    .ieee-sites-main-nav.is-open::before {
        transform: skew(7deg) translateX(80px);
    }

    .ieee-sites-main-nav ul {
        transform: translateX(80px) skew(7deg)translateY(-120px);
    }

    .ieee-sites-main-nav li {
        font-size: 14px;
    }
}

/* Styles for devices between 400px and 300px */
@media (max-width: 400px) {
    .ieee-sites-main-nav.is-open::before {
        transform: skew(6deg) translateX(60px);
    }

    .ieee-sites-main-nav ul {
        transform: translateX(65px) skew(6deg) translateY(-100px);
    }

    .ieee-sites-main-nav li {
        font-size: 14px;
    }
}

@media (max-width: 380px) and (max-height: 670px) {
    .ieee-sites-main-nav.is-open::before {
        transform: skew(6deg) translateX(60px);
    }

    .ieee-sites-main-nav ul {
        transform: translateX(40px) skew(6deg) translateY(-50px);
    }

    .ieee-sites-main-nav li {
        font-size: 12px;
    }

    .ieee-sites-main-nav ul,
    .ieee-sites-main-nav li {
        list-style: none;
        padding: 18px;
    }
}

@media (max-width: 350px) {
    .ieee-sites-main-nav.is-open::before {
        transform: skew(6deg) translateX(60px);
    }

    .ieee-sites-main-nav ul {
        transform: translateX(70px) skew(6deg) translateY(-120px);
    }

    .ieee-sites-main-nav li {
        font-size: 12px;
    }
}

/* Styles for devices below 300px */
@media (max-width: 300px) {
    .ieee-sites-main-nav.is-open::before {
        transform: skew(5deg) translateX(40px);
    }

    .ieee-sites-main-nav ul {
        transform: translateX(20px) skew(5deg);
    }
}

/* Styles for devices below 200px */
@media (max-width: 200px) {
    .ieee-sites-main-nav.is-open::before {
        transform: skew(3deg) translateX(20px);
    }

    .ieee-sites-main-nav ul {
        transform: translateX(10px) skew(4deg);
    }
}

.ieee-sites-back-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    background: none;
    border: none;
    color: white;
    font-size: 1.2em;
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 10px;
    z-index: 2000;
    /* Ensure it's above other elements */
}


.ieee-sites-back-btn:hover {
    color: rgb(255, 104, 104);
}

.ieee-sites-back-btn svg {
    margin-right: 8px;
}