.topnav {
  background: #e8f2f2;
  border-bottom: 1.7px solid #000;
  padding: 12px;
}

.topnav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.topnav-menu {
  list-style: none;
  display: flex;
  position: relative;
}

.topnav-menu li {
  margin-left: 1rem;
}

.topnav-menu p {
  margin: 0;
  color: #999;
}

.topnav-menu a {
  color: rgb(58, 152, 247);
  font-weight: bolder;
  text-decoration: none;
  font-size: 1rem;
}

.topnav-menu a:hover {
  color: rgb(32, 56, 192);
  text-decoration: underline;
}

/* Responsive Design */

/* For screen widths of 600px and below */
@media (max-width: 600px) {
  .topnav {
    padding: 8px; /* Reduce topnav padding */
  }

  .topnav-menu a {
    font-size: 0.8rem; /* Reduce font size */
  }

  .topnav-menu li {
    margin-left: 0.5rem; /* Reduce spacing between menu items */
  }
}

/* For screen widths below 500px, hide the menu */
@media (max-width: 768px) {
  .topnav{
    display: none;
  }
  .topnav-menu {
    display: none; /* Hide the menu */
  }
}
