/* Member.css */

.member-container{
    position: relative;
}

.member-head {
    margin: 0;
    padding: 0;
    font-family: 'Poppins';
    text-align: center;
    font-size: 5rem;
    padding-top: 20px;
}

.professional-members-head {
    text-align: center;
    margin: 0;
    padding: 0;
    padding-top: 50px;
    padding-bottom: 30px;
    font-size: 32px;
}

.student-members-head {
    text-align: center;
    margin: 0;
    padding: 0;
    padding-top: 30px;
    font-size: 30px;
}

.student-member-switch {
    text-align: center;
    margin: 20px 0;
}

.switch {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0;
    flex-wrap: wrap;
}

.switch li {
    margin-top: 30px;
    cursor: pointer;
    font-size: 1.3em;
    text-transform: uppercase;
    font-weight: bold;
    color: #333;
    position: relative;
    transition: color 0.3s ease;
}

.switch li:not(:last-child)::after {
    content: "||";
    margin-left: 20px;
    margin-right: 20px;
    color: #666;
}

.switch li:hover {
    color: #0073e6;
}

/* Active link styling */
.switch li.active {
    color: #c71585; /* Red-violet color */
}

.un {
    display: inline-block;
    padding-bottom: 2px;
    background-image: linear-gradient(#6200ff 0 0);
    background-position: 0 100%;
    background-size: 0% 2.5px;
    background-repeat: no-repeat;
    transition:
        background-size 0.3s,
        background-position 0s 0.3s;
}

.un:hover {
    transition: 500ms;
    background-position: 100% 100%;
    background-size: 100% 2.5px;
    background-image: linear-gradient(rgb(115, 0, 255) 0 0);
}

/* Responsive adjustments */
@media (max-width: 900px) {
    .member-head {
        font-size: 4rem;
    }

    .switch li {
        font-size: 1.2em;
    }

    .professional-members-head, .student-members-head{
        font-size: 28px;
    }
}

@media (max-width: 700px) {
    .member-head {
        font-size: 4rem;
    }

    .switch li {
        font-size: 15px;
    }

    .professional-members-head, .student-members-head{
        font-size: 25px;
    }
}

@media (max-width: 550px) {
    .member-head {
        font-size: 3.5rem;
    }

    .switch li {
        font-size: 11.5px;
    }

    .switch li:not(:last-child)::after{
        content: '|';
        margin-left: 10px;
        margin-right: 10px;
    }

    .student-members-head{
        font-size: 23px;
    }
}

@media (max-width: 390px) {
    .member-head {
        font-size: 3rem;
    }

    .switch li {
        font-size: 10px;
    }

    .switch li:not(:last-child)::after{
        content: '|';
        margin-left: 6px;
        margin-right: 6px;
    }

    .student-members-head{
        font-size: 21px;
    }
}
