@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

.container-IEEE {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    position: relative;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.6);
}

.content {
    position: relative;
    z-index: 1;
    color: white;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.6); /* Dark background to ensure text visibility */
}

.head-IEEE {
    text-transform: uppercase;
    text-align: center;
    padding: 20px;
    font-size: 2.5rem;
    padding-bottom: 50px;
}

.para-IEEE {
    padding: 0 3%;
    text-align: justify;
    font-size: 1.5rem;
    padding-bottom: 70px;
}

.flex-contain {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 20px;
}

.IEEE-Membership, .IEEE-Society {
    flex: 1;
    margin: 20px;
    text-align: justify;
    font-size: 1rem;
    position: relative;
    padding: 30px;
    box-sizing: border-box;
    color: #FFF;
    background: #000000b2;
    background-clip: padding-box;
    border: solid 5px transparent;
    border-radius: 1em;
    width: 90%;
}

.IEEE-Membership:before, .IEEE-Society:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    margin: -4px;
    border-radius: inherit;
    background: linear-gradient(to right, rgba(165, 97, 255, 0.7), rgba(133, 194, 255, 0.7));
}

.IEEE-Society:before {
    background: linear-gradient(to right, rgba(133, 194, 255, 0.7), rgba(165, 97, 255, 0.7));
}

.video-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
    background-size: cover;
    pointer-events: none; /* Prevent the video from interfering with content interaction */
}

#background-video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    transform: translate(-50%, -50%);
    z-index: -10;
    pointer-events: none; /* Prevent the video from interfering with content interaction */
}

/* Make the font size responsive */
@media (max-width: 2000px) {
    .head-IEEE {
        font-size: 3rem;
    }
    .para-IEEE {
        font-size: 2rem;
    }
    .IEEE-Membership, .IEEE-Society {
        font-size: 1.2rem;
    }
    .IEEE-Membership h4, .IEEE-Society h4{
        font-size: 24px;
    }
}

@media (max-width: 1600px) {
    .head-IEEE {
        font-size: 2.5rem;
    }
    .para-IEEE {
        font-size: 1.5rem;
    }
    .IEEE-Membership, .IEEE-Society {
        font-size: 1.1rem;
    }
    .IEEE-Membership h4, .IEEE-Society h4{
        font-size: 23px;
    }
}

@media (max-width: 1200px) {
    .head-IEEE {
        font-size: 2.3rem;
    }
    .para-IEEE {
        font-size: 1.2rem;
    }
    .IEEE-Membership, .IEEE-Society {
        font-size: 0.9rem;
    }
    .IEEE-Membership h4, .IEEE-Society h4{
        font-size: 22px;
    }
}

@media (max-width: 1000px) {
    .head-IEEE {
        font-size: 2rem;
    }
    .para-IEEE {
        font-size: 1.1rem;
    }
    .IEEE-Membership, .IEEE-Society {
        font-size: 0.8rem;
    }
    .IEEE-Membership h4, .IEEE-Society h4{
        font-size: 21px;
    }
}

@media (max-width: 767px) {
    .flex-contain {
        flex-direction: column; /* Change to column layout */
    }
    .head-IEEE {
        font-size: 1.6rem;
    }
    .para-IEEE {
        font-size: 1.15rem;
    }
    .IEEE-Membership, .IEEE-Society {
        font-size: 0.85rem;
    }
    .IEEE-Membership h4, .IEEE-Society h4{
        font-size: 19px;
    }
}

@media (max-width: 600px) {
    .head-IEEE {
        font-size: 1.4rem;
    }
    .para-IEEE {
        font-size: 1rem;
    }
    .IEEE-Membership, .IEEE-Society {
        font-size: 0.78rem;
    }
    .IEEE-Membership h4, .IEEE-Society h4{
        font-size: 18px;
    }
}

@media (max-width: 450px) {
    .head-IEEE {
        font-size: 1.2rem;
    }
    .para-IEEE {
        font-size: 0.8rem;
    }
    .IEEE-Membership, .IEEE-Society {
        padding: 15px;
        font-size: 0.6rem;
    }
    .IEEE-Membership{
        margin-top: -50px;
    }
    .IEEE-Membership h4, .IEEE-Society h4{
        font-size: 16px;
    }
}
