.collab-container {
    margin: 0;
    background-color: rgb(221, 255, 244);
}

.collab-head {
    font-family: 'ICA Rubrik';
    font-size: 6em;
    line-height: 1em;
    color: #25283B;
    position: relative;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 50px;
    padding-top: 100px;
}

.collab-sub-container {
    width: min(1500px, 120vw);
    margin: auto;
}

.slider-collab {
    width: 100%;
    height: var(--height-collab);
    overflow: hidden;
    mask-image: linear-gradient(
        to right,
        transparent,
        #000 10% 90%,
        transparent
    );
    padding-bottom: 80px;
}

.slider-collab .list-collab {
    display: flex;
    width: 100%;
    min-width: calc(var(--width-collab) * var(--quantity-collab));
    position: relative;
}

.slider-collab .list-collab .item-collab {
    width: var(--width-collab);
    height: var(--height-collab);
    position: absolute;
    left: 100%;
    animation: autorun 40s linear infinite;
    transition: filter 0.5s;
    animation-delay: calc((40s / var(--quantity-collab)) * (var(--position-collab) - 1) - 40s)!important;
}

.slider-collab .list-collab .item-collab img {
    width: 100%;
}

@keyframes autorun {
    from {
        left: 100%;
    }
    to {
        left: calc(var(--width-collab) * -1);
    }
}

.slider-collab:hover .item-collab {
    animation-play-state: paused!important;
    filter: grayscale(0.4);
}

.slider-collab .item-collab:hover {
    filter: grayscale(0);
}

/* Responsive design starts here */

@media screen and (max-width: 1500px) {
    .collab-head {
        font-size: 7rem;
    }
    .collab-sub-container {
        width: min(1300px, 100vw);
        margin: auto;
    }
}

@media screen and (max-width: 1200px) {
    .collab-head {
        font-size: 6rem;
    }
    .collab-sub-container {
        width: min(1200px, 90vw);
        margin: auto;
    }
}

@media screen and (max-width: 1000px) {
    .collab-head {
        font-size: 5.5rem;
    }
}

@media screen and (max-width: 800px) {
    .collab-head {
        font-size: 4rem;
        padding-bottom: 50px;
    }
    
}

@media screen and (max-width: 600px) {
    .collab-head {
        font-size: 3rem;
        padding-top: 30px;
    }
    
}

@media screen and (max-width: 400px) {
    .collab-head {
        font-size: 2.4rem;
        padding-top: 50px;
    }
}

@media screen and (max-width: 300px) {
    .collab-head {
        font-size: 3.6rem;
        padding-top: 10px;
    }
}