/* Container styling with animated star background */
#adminContainer {
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
  background: radial-gradient(ellipse at bottom, #000000 0%, #090A0F 100%);
}

/* Star layers */
#stars, #stars2, #stars3 {
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 2px;
  border-radius: 10px;
  background: transparent;
  pointer-events: none;
}

