  font-size: 1.2em;
  border: 2px solid #333;
  border-radius: 10px;
  box-shadow: 0px 0px 8px rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
}

.function-row:hover {
  transform: scale(1.02);
  box-shadow: 0px 0px 12px rgba(255, 255, 255, 0.5);
}

.close-btn, .back-btn {
