/* Form Section Styling */
#formSection {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

#adminForm {
  position: relative;
  background: rgba(0, 0, 0, 1);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 1);
  width: 400px;
  height: 300px;
  transform: translateX(200px);
  font-family: 'Helvetica Neue', Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  backdrop-filter: blur(6px);
}

/* Luxurious Header Styling */
.admin-header {
  color: white;
  font-size: 3rem;
  font-family: 'ICA Rubrik';
  text-align: center;
  padding-bottom: 10px;
  font-weight: 300;
  margin-bottom: 30px;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* Input container styling */
.input-container {
  position: relative;
  width: 100%;
  height: fit-content;
  margin-bottom: 25px;
}

/* Luxurious Input Field Styling */
#adminForm input {
  width: 94%;
  padding: 12px;
  margin-bottom: 10px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.05);
  color: white;
  font-size: 1rem;
  outline: none;
  transition: all 0.3s ease;
  font-weight: 400;
}

#adminForm input:focus {
  background-color: rgba(12, 0, 0, 1);
  border-color: rgba(255, 255, 255, 0.7);
  box-shadow: 0 4px 8px rgba(255, 255, 255, 0.3);
}

/* Floating Label Styling */
#adminForm label {
  position: absolute;
  top: 12px;
  left: 15px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 1rem;
  font-family: 'ICA Rubrik';
  pointer-events: none;
  transition: all 0.3s ease;
  background-color: rgba(0, 0, 0, 1);
  padding: 0 5px;
  border-radius: 4px;
}

/* Label float effect on input focus */
#adminForm input:focus + label,
#adminForm input:not(:placeholder-shown) + label {
  top: -12px;
  left: 10px;
  font-size: 0.8rem;
  color: #ffffff;
  padding: 0 8px;
  font-family: 'ICA Rubrik';
  border-radius: 4px;
  box-shadow: 0 2px 4px rgba(255, 255, 255, 0.3);
}

/* Luxurious Submit Button Styling */
#adminForm button {
  width: 100%;
  padding: 12px;
  background-color: #5a67d8;
  border: none;
  border-radius: 8px;
  color: white;
  text-transform: uppercase;
  font-family: 'ICA Rubrik';
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

#adminForm button:hover {
  background-color: #4c51bf;
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
}

#adminForm button:active {
  background-color: #4349a3;
  transform: translateY(0);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
}

